@keyframes rainbow {
  0% { background-color: #ff003c; }
  16% { background-color: #fffc00; }
  33% { background-color: #00ff5e; }
  50% { background-color: #00cfff; }
  66% { background-color: #2e00ff; }
  83% { background-color: #ff00c8; }
  100% { background-color: #ff003c; }
}
.rainbow-bg {
  animation: rainbow 1.5s linear 0s;
}
